'use client';

import { CardStackCarousel } from "@/components/CardStackCarousel";
import { BlackBackgroundCarousel } from "@/components/BlackBackgroundCarousel";
import { WhiteBackgroundCarousel } from "@/components/WhiteBackgroundCarousel";
import { motion, useAnimation, useInView } from "framer-motion";
import { useEffect, useRef } from "react";
import ExpandingPosterCarousel from "@/components/ExpandingPosterCarousel";
import TimedCardOpening from "@/components/TimedCardOpening";

export default function CarouselPage() {
  const controls = [useAnimation(), useAnimation(), useAnimation(), useAnimation()];
  const refs = [useRef(null), useRef(null), useRef(null), useRef(null)];
  const inViews = refs.map((ref) => useInView(ref, { amount: 0.3, once: false }));

  useEffect(() => {
    inViews.forEach((inView, index) => {
      if (inView) {
        controls[index].start({
          opacity: 1,
          y: 0,
          transition: {
            duration: 1.2,
            type: "spring",
            stiffness: 80,
            damping: 20,
          },
        });
      } else {
        controls[index].start({ opacity: 0, y: 30 });
      }
    });
  }, [inViews, controls]);

  return (
    <div className="h-screen w-full overflow-y-scroll snap-y snap-mandatory">
      {/* Section 1 */}
      <motion.section
        ref={refs[0]}
        className="h-screen snap-start"
        initial={{ opacity: 0, y: 30 }}
        animate={controls[0]}
      >
        <CardStackCarousel />
      </motion.section>

      {/* Section 2 */}
      <motion.section
        ref={refs[1]}
        className="h-screen snap-start"
        initial={{ opacity: 0, y: 30 }}
        animate={controls[1]}
      >
        <ExpandingPosterCarousel />
      </motion.section>

      {/* Section 3 */}
      <motion.section
        ref={refs[2]}
        className="h-screen snap-start"
        initial={{ opacity: 0, y: 30 }}
        animate={controls[2]}
      >
        <BlackBackgroundCarousel />
      </motion.section>

      {/* Section 4 */}
      <motion.section
        ref={refs[3]}
        className="h-screen snap-start"
        initial={{ opacity: 0, y: 30 }}
        animate={controls[3]}
      >
        <WhiteBackgroundCarousel />
      </motion.section>

      {/* Section 5 */}
      <motion.section
        ref={refs[4]}
        className="h-screen snap-start"
        initial={{ opacity: 0, y: 30 }}
        animate={controls[4]}
      >
        <TimedCardOpening />
      </motion.section>
    </div>
  );
}
